// 
.navbar {
  // 
  .navbar-toggler {
    color: rgba(0, 0, 0, .9);
    border-color: transparent;
  }

  // 
  .dropdown-toggle {
    color: $gray-900;
    text-decoration: none;
  }
}

// 
.dropdown-menu {
  line-height: 2.2;
}

// 
.dropdown-item.active {
  color: #fff !important;
}

// 
.page-link {
  &:hover, &:focus {
    text-decoration: none;
  }
}

// 
.page-item.active {
  .page-link {
    min-width: 40px;
    text-align: center;

    &:hover {
      cursor: pointer;
    }
  }
}

// 
.blockquote {
  background-color: $blockquote-bg;
}

// 
.form-check-inline {
  vertical-align: middle;
}

// 
.custom-select-sm {
  padding: 0 ($custom-select-padding-x * .9 + $custom-select-indicator-padding) 0 $custom-select-padding-x * .9;
  line-height: $custom-select-line-height * .95;
}