// 
html {
  font-size: 14px;
}

// 
$font-size-xl: 1.5rem; // ~21px
$font-size-xs: 0.75rem; // 10.5px

// 
$spacer: 1rem;
// 
$spacer-2: $spacer * .5; // ~7px
// 
$spacer-1: $spacer * .25; // ~3.5px

// 
$white: #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black: #000;

$blue: #007bff;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$red: #dc3545;
$orange: #fd7e14;
$yellow: #ffc107;
$green: #28a745;
$teal: #20c997;
$cyan: #17a2b8;

// 
$bg-opacity: .5;
$bg-opacity-light: .6;
$bg-opacity-lighter: .7;
$bg-opacity-lightest: .95;

// 
$body-bg: #fcfcfc;

// 
$list-group-bg: rgba($white, $bg-opacity);
// 
$list-group-hover-bg: rgba(darken($gray-100, 5%), $bg-opacity);

// 
$breadcrumb-bg: rgba(darken($gray-200, 5%), $bg-opacity);

// 
$nav-tabs-link-active-bg: rgba($body-bg, $bg-opacity-light);
// 
$nav-tabs-link-active-border-color: darken($gray-300, 3%) darken($gray-300, 3%) $nav-tabs-link-active-bg;

// 
$card-bg: rgba($white, $bg-opacity);
// 
$pagination-bg: rgba($white, $bg-opacity-light);
// 
$pagination-disabled-bg: rgba($white, $bg-opacity-light);

// 
$input-bg: rgba($white, $bg-opacity-light);
// 
$custom-select-bg: rgba($white, $bg-opacity-light);

// 
$modal-header-border-color: darken($gray-200, 7%);

// 
$blockquote-bg: rgba(darken($body-bg, 7%), $bg-opacity-lighter);
// 
$blockquote-border-width: .3rem;
// 
$blockquote-border-color: darken($body-bg, 11%);