// 
#postPage {
  .breadcrumb {
    li:nth-child(2) {
      max-width: 190px;
    }
  }
}

// 
.editor-btn-group {
  .btn {
    margin-bottom: 5px;
  }
}

// 
#fontSizeDropdownMenu {
  line-height: 1.3;
}

// 
.select-color-menu {
  min-width: 234px;
  padding: {
    left: $spacer-2;
    right: $spacer-2;
  }
  line-height: 1.6;

  span {
    display: inline-block;
    width: 18px;
    height: 18px;
    cursor: pointer;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.08);
  }

  hr {
    margin: {
      top: $spacer-2;
      bottom: $spacer-2;
    }
    border: none;
  }
}

// 
.latest-floor-content {
  font-size: $font-size-sm;

  // 
  fieldset {
    legend {
      font-size: $small-font-size;
    }
  }
}

// 
.smile-panel {
  width: 266px;
  height: 188px;
  overflow-y: auto;
  opacity: .9;

  img {
    margin: 3px;
    cursor: pointer;
  }
}

// 
#postContent, #msgContent {
  word-break: break-all;

  @include media-breakpoint-down(md) {
    & {
      font-size: .875rem; // ~12.25px
    }
  }
}